.about {
  &__title {
    margin-bottom: 24px;
  }

  &__description {
    margin: 0 auto;

    @extend %OpenSans600;
    font-size: 22px;
    line-height: 150%;

    text-align: center;
    color: $main-text-color;

    @include onTablet {
      font-size: 24px;
    }

    @include onSmallDesktop {
      width: 716px;
    }

    @include onDesktop {
      width: 754px;
    }
  }
}
